.react-tagsinput {
  background-color: #fff;
  border: 1px solid #ccc;
  overflow: hidden;
  padding-left: 5px;
  padding-top: 5px;
}

.react-tagsinput--focused {
  border-color: #a5d24a;
}

.react-tagsinput-tag {
  font-family: 'Syne', serif-sans;
  
  background-color: #4338ca;
  border-radius: 10px;
  border: 1px solid #a5d24a;
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px;
}

.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}

.react-tagsinput-tag a::before {
  content: " Ã—";
}

.react-tagsinput-input {
  font-family: 'Syne', serif-sans;

  background: transparent;
  border: 0;
  color: #000;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 6px;
  margin-top: 1px;
  outline: none;
  padding: 5px;
  width: 80px;
}
